import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';
import * as Constants from '../../utily/constants';
import {ProductMaker} from '../../model/product/product.model';

/**
 * Generated class for the AddProductPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-product',
  templateUrl: 'add-product.html',
})
export class AddProductPage {
  product = ProductMaker.create({
    name:'1',
    originalPrice: 0,
    sellingPrice: 0,
    shippingCost: 0,
    discount: 0,
    couponCode:'aa',
    picture: 'pic',
    sold:true
  });
  
//   product = new ProductMaker({
//     name: req.body.name,
//     originalPrice: req.body.originalPrice,
//     sellingPrice: req.body.sellingPrice,
//     shippingCost: req.body.shippingCost,
//     discount: req.body.discount,
//     couponCode:req.body.couponCode,
//     picture: req.body.picture,
//     sold:req.body.sold
// });
  	
  constructor(public navCtrl: NavController, public navParams: NavParams, private restProvider : RestProvider) {
    console.log(this.product);
  }

  saveProduct() {
    console.log(this.product);
    this.restProvider.postData(this.product, Constants.ApiEndPoint.PRODUCTS_ENSPOINT).then((result) => {
      console.log(result);
    }, (err) => {
      console.log(err);
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddProductPage');
  }

}	



